.bgForm {
  border-radius: 40px;
  padding-top: 20px;
  padding-right: 8px;
  padding-left: 8px;
  padding-bottom: 17px;

  background-color: #ffffff;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
}

.header {
  margin-bottom: 24px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.35;
  color: #111111;
}

.buttonContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 24px;
}

@media screen and (min-width: 768px) {
  .bgForm {
    max-width: 458px;
    padding-top: 20px;
    padding-right: 32px;
    padding-left: 32px;
    padding-bottom: 20px;
    margin-right: auto;
    margin-left: auto;
  }

  .bgFormMidle {
    max-width: 704px;
  }

  .header {
    font-size: 28px;
    text-align: center;
  }

  .formContainer {
    display: flex;
    justify-content: space-between;
  }

  .firstChild {
    width: 200px;
  }

  .secondChild {
    width: 395px;
  }

  .addPhoto {
    display: block;
  }

  .addPhoto p {
    max-width: 200px;
    margin-bottom: 8px;
  }

  .buttonContainer {
    align-items: center;
    justify-content: center;
    flex-direction: row-reverse;
    margin-top: 40px;
    margin-bottom: 20px;
  }

  .buttonContainerSecond {
    justify-content: center;
    justify-content: center;
    flex-direction: row-reverse;
    margin-top: 17px;
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 1280px) {
  .bgFormMidle {
    max-width: 822px;
    padding-left: 75px;
    padding-right: 75px;
  }

  .comments {
    height: 110px;
  }
}
